<template>
    <div>
        <div class="cm-panel" v-if="marginInfo">
            <h3 class="margin-b-14">保证金信息</h3>
            <Form ref="marginInfo" :model="marginInfo" :label-width="120">
                <Row>
                    <i-col :xs="24" :sm="12" :md="8" :lg="6">
                        <FormItem label="保证金金额">
                            <span class="spanline32">{{marginInfo.marginAmount|amount}}</span>
                        </FormItem>
                    </i-col>
                    <i-col :xs="24" :sm="12" :md="8" :lg="6">
                        <FormItem label="保证金(%)">
                            <span class="spanline32">{{marginInfo.marginProportion|amount}}</span>
                        </FormItem>
                    </i-col>
                    <i-col :xs="24" :sm="12" :md="8" :lg="6">
                        <Form-item label="保证金是否冲抵">
                            <span class="spanline32">{{marginInfo.marginIsOffsetName}}</span>
                        </Form-item>
                    </i-col>
                </Row>
            </Form>
        </div>
    </div>
</template>
<script>
    /**
     * @Description: 保证金
     */

    import base from './js/loanbase.js';

    export default {
        name: 'marginInfo',
        mixins: [base],
        props: {},
        components: {},
        directives: {},
        data() {
            return {}
        },
        computed: {},
        mounted() {
            if (this.baseInfo.hasMargin == '1') {
                this.queryInitData('marginInfo', () => {
                    this.$nextTick(() => {
                        this.todoRecount();
                    })
                })
            }
        },
        methods: {},
    }
</script>



