<template>
    <div>
        <div class="cm-panel" v-if="calcInfo">
            <h3 class="margin-b-14">贷款计算</h3>
            <Form ref="calcInfo" :model="calcInfo" :label-width="120"
                  :show-message="true">
                <Row>
                    <i-col :xs="24" :sm="12" :md="8" :lg="6">
                        <FormItem label="车辆销售价(元)">
                            <span class="spanline32">{{sellingAmount|amount}}</span>
                        </FormItem>
                    </i-col>
                    <i-col :xs="24" :sm="12" :md="8" :lg="6">
                        <FormItem label="首付金额(元)">
                            <span class="spanline32">{{calcInfo.downPaymentAmount|amount}}</span>
                        </FormItem>
                    </i-col>
                    <i-col :xs="24" :sm="12" :md="8" :lg="6">
                        <FormItem label="首付比例(%)">
                            <span class="spanline32">{{calcInfo.downPaymentProportion|amount}}</span>
                        </FormItem>
                    </i-col>
                    <i-col :xs="24" :sm="12" :md="8" :lg="6">
                        <FormItem label="尾付金额(元)">
                            <span class="spanline32">{{calcInfo.finalPaymentAmount|amount}}</span>
                        </FormItem>
                    </i-col>
                    <i-col :xs="24" :sm="12" :md="8" :lg="6">
                        <FormItem label="尾付比例(%)">
                            <span class="spanline32">{{calcInfo.finalPaymentProportion|amount}}</span>
                        </FormItem>
                    </i-col>
                </Row>
            </Form>

            <div>
                <div class="info-title-header padding-t-15"><span class="info-title-title">贷款方案</span></div>
                <i-table class="table-only-horizon-line margin-t-10" :total="calcres.total"
                         :loading="calcres.loading"
                         border :columns="calcres.columns" :data="calcres.data" v-if="showCalcRes"></i-table>
            </div>
        </div>
    </div>
</template>
<script>
    /**,
     * @Description: 贷款计算
     */
    import base from './js/loanbase.js';

    //floatMul乘， floatDiv除， floatAdd加，floatSub减
    import {yx, floatMul, floatDiv, floatAdd, floatSub} from '@/utils/helper.js';
    import vFilter from '@/utils/filters';

    export default {
        name: 'orderLoanCalcForm',
        mixins: [base],
        props: {},
        components: {},
        data() {
            return {
                //贷款试算表中数据
                showCalcRes: true,
                calcres: {
                    title: '贷款试算结果',
                    showModal: false,
                    exportLoading: false,
                    loading: false,
                    total: 0,
                    data: [],
                    columns: [
                        {
                            title: '金融机构',
                            key: 'capitalName',
                            render: (h, params) => {
                                return h('div', params.row.capitalName || '--')
                            },
                        },
                        {
                            title: '客户融资额(元)',
                            key: 'custFinanceAmount',
                            render: (h, params) => {
                                return h('div', vFilter.number(params.row.custFinanceAmount) || '--')
                            },
                        },
                        {
                            title: '风控融资额(元)',
                            key: 'riskControlFinanceAmount',
                            render: (h, params) => {
                                return h('div', vFilter.number(params.row.riskControlFinanceAmount) || '--')
                            },
                        },
                        {
                            title: '风险融资额(元)',
                            key: 'riskFinanceAmount',
                            render: (h, params) => {
                                return h('div', vFilter.number(params.row.riskFinanceAmount) || '--')
                            }
                        },
                        {
                            title: '客户利率(%)',
                            key: 'custInterestRate',
                            render: (h, params) => {
                                return h('div', vFilter.number(params.row.custInterestRate) || '--')
                            }
                        },
                        {
                            title: '客户费率(%)',
                            key: 'custCostRate',
                            render: (h, params) => {
                                var rate = params.row.custCostRate;
                                return h('span', {
                                    style: {
                                        'color': rate < 0 ? '#F5222D ' : ''
                                    }
                                }, vFilter.number(rate) || '--')
                            }
                        },
                        {
                            title: '月供金额(元)',
                            key: 'custMonthlyAmount',
                            render: (h, params) => {
                                return h('div', vFilter.number(params.row.custMonthlyAmount) || '--')
                            }
                        },
                        {
                            title: '放款金额(元)',
                            key: 'loanPayableAmount',
                            render: (h, params) => {
                                //自营时显示放款金额，其他资方计算不准备暂不显示
                                if (params.row.capitalCode == 'YX')
                                    return h('div', vFilter.number(params.row.loanPayableAmount) || '--')
                                else
                                    return h('div', '--')
                            }
                        },
                    ]
                },
            }
        },
        computed: {},
        mounted() {
            this.queryInitData('calcInfo', () => {
                //回显自营结果，查看时不显示金融机构
                var listData = this.calcInfo.financeCalculationTableList;
                if (!listData) listData = [];
                this.calcres.data = listData;
                this.showCalcRes = true;
                //统计一次
                this.$nextTick(() => {
                    this.todoRecount();
                })
            })
        },
        methods: {},
    }
</script>



